'use client';

import { useEffect, useState } from 'react';
import { ChevronUp, ChevronDown, Filter, Download, Flag, CheckCircle2, Clock, Send, Trash2 } from 'lucide-react';

interface Report {
  id: number;
  lat: number;
  lng: number;
  category: string;
  severity: 'Low' | 'Medium' | 'High';
  note: string;
  reporter: string;
  date: string;
  zone: string;
  upvotes: number;
  verified: boolean;
  imagePlaceholder: string;
}

type SortField = 'id' | 'date' | 'severity' | 'upvotes';
type SortOrder = 'asc' | 'desc';

export default function EvidenceTable() {
  const [reports, setReports] = useState<Report[]>([]);
  const [sortField, setSortField] = useState<SortField>('date');
  const [sortOrder, setSortOrder] = useState<SortOrder>('desc');
  const [filterStatus, setFilterStatus] = useState<'all' | 'verified' | 'pending'>('all');
  const [filterSeverity, setFilterSeverity] = useState<'all' | 'High' | 'Medium' | 'Low'>('all');

  useEffect(() => {
    fetch('/data/mock-reports.json')
      .then((res) => res.json())
      .then((data) => setReports(data));
  }, []);

  // Filter and sort reports
  const filteredReports = reports
    .filter((r) => {
      if (filterStatus === 'verified') return r.verified;
      if (filterStatus === 'pending') return !r.verified;
      return true;
    })
    .filter((r) => {
      if (filterSeverity === 'all') return true;
      return r.severity === filterSeverity;
    })
    .sort((a, b) => {
      let aVal: any = a[sortField];
      let bVal: any = b[sortField];

      if (sortField === 'date') {
        aVal = new Date(a.date).getTime();
        bVal = new Date(b.date).getTime();
      }

      if (aVal < bVal) return sortOrder === 'asc' ? -1 : 1;
      if (aVal > bVal) return sortOrder === 'asc' ? 1 : -1;
      return 0;
    });

  const toggleSort = (field: SortField) => {
    if (sortField === field) {
      setSortOrder(sortOrder === 'asc' ? 'desc' : 'asc');
    } else {
      setSortField(field);
      setSortOrder('desc');
    }
  };

  const SortIcon = ({ field }: { field: SortField }) => {
    if (sortField !== field) return <ChevronUp className="w-3 h-3 opacity-30" />;
    return sortOrder === 'asc' ? (
      <ChevronUp className="w-3 h-3" />
    ) : (
      <ChevronDown className="w-3 h-3" />
    );
  };

  const getSeverityColor = (severity: string) => {
    return severity === 'High'
      ? { bg: '#fca5a5', text: '#7f1d1d' }
      : severity === 'Medium'
        ? { bg: '#fcd34d', text: '#78350f' }
        : { bg: '#c6f6d5', text: '#15803d' };
  };

  return (
    <div className="p-6 space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <h1 className="text-3xl font-bold text-gray-900">Evidence Management</h1>
        <button className="flex items-center gap-2 py-2 px-4 rounded-lg border border-gray-300 hover:bg-gray-100 text-sm font-medium">
          <Download className="w-4 h-4" />
          Export CSV
        </button>
      </div>

      {/* Filters */}
      <div className="bg-white rounded-lg border border-gray-200 p-4 flex gap-4">
        <div className="flex items-center gap-2">
          <Filter className="w-4 h-4 text-gray-600" />
          <span className="text-sm font-medium text-gray-700">Filter:</span>
        </div>

        <select
          value={filterStatus}
          onChange={(e) => setFilterStatus(e.target.value as typeof filterStatus)}
          className="px-3 py-1 text-sm border border-gray-300 rounded-lg hover:border-gray-400"
        >
          <option value="all">All Status</option>
          <option value="verified">Verified</option>
          <option value="pending">Pending</option>
        </select>

        <select
          value={filterSeverity}
          onChange={(e) => setFilterSeverity(e.target.value as typeof filterSeverity)}
          className="px-3 py-1 text-sm border border-gray-300 rounded-lg hover:border-gray-400"
        >
          <option value="all">All Severity</option>
          <option value="High">High</option>
          <option value="Medium">Medium</option>
          <option value="Low">Low</option>
        </select>

        <div className="ml-auto text-sm text-gray-600">
          {filteredReports.length} report{filteredReports.length !== 1 ? 's' : ''}
        </div>
      </div>

      {/* Table */}
      <div className="bg-white rounded-lg border border-gray-200 overflow-hidden">
        <table className="w-full">
          <thead className="bg-gray-50 border-b border-gray-200">
            <tr>
              <th className="px-4 py-3 text-left">
                <button
                  onClick={() => toggleSort('id')}
                  className="font-semibold text-gray-900 text-sm flex items-center gap-1 hover:text-gray-600"
                >
                  ID <SortIcon field="id" />
                </button>
              </th>
              <th className="px-4 py-3 text-left font-semibold text-gray-900 text-sm">Image</th>
              <th className="px-4 py-3 text-left font-semibold text-gray-900 text-sm">Category</th>
              <th className="px-4 py-3 text-left">
                <button
                  onClick={() => toggleSort('severity')}
                  className="font-semibold text-gray-900 text-sm flex items-center gap-1 hover:text-gray-600"
                >
                  Severity <SortIcon field="severity" />
                </button>
              </th>
              <th className="px-4 py-3 text-left font-semibold text-gray-900 text-sm">Location</th>
              <th className="px-4 py-3 text-left font-semibold text-gray-900 text-sm">Reporter</th>
              <th className="px-4 py-3 text-left">
                <button
                  onClick={() => toggleSort('date')}
                  className="font-semibold text-gray-900 text-sm flex items-center gap-1 hover:text-gray-600"
                >
                  Date <SortIcon field="date" />
                </button>
              </th>
              <th className="px-4 py-3 text-left font-semibold text-gray-900 text-sm">Status</th>
              <th className="px-4 py-3 text-left font-semibold text-gray-900 text-sm">Actions</th>
            </tr>
          </thead>
          <tbody className="divide-y divide-gray-200">
            {filteredReports.map((report) => (
              <tr key={report.id} className="hover:bg-gray-50 transition-colors">
                <td className="px-4 py-3 text-sm font-medium text-gray-900">#{report.id}</td>
                <td className="px-4 py-3">
                  <img
                    src={report.imagePlaceholder || "/placeholder.svg"}
                    alt="Evidence"
                    className="w-12 h-12 rounded object-cover"
                  />
                </td>
                <td className="px-4 py-3 text-sm text-gray-700">{report.category}</td>
                <td className="px-4 py-3">
                  <span
                    className="text-xs font-bold rounded-full px-2 py-1"
                    style={getSeverityColor(report.severity)}
                  >
                    {report.severity}
                  </span>
                </td>
                <td className="px-4 py-3 text-sm text-gray-700">{report.zone}</td>
                <td className="px-4 py-3 text-sm text-gray-700">{report.reporter}</td>
                <td className="px-4 py-3 text-sm text-gray-700">
                  {new Date(report.date).toLocaleDateString()}
                </td>
                <td className="px-4 py-3">
                  {report.verified ? (
                    <span className="flex items-center gap-1 text-xs font-medium text-green-700">
                      <CheckCircle2 className="w-4 h-4" /> Verified
                    </span>
                  ) : (
                    <span className="flex items-center gap-1 text-xs font-medium text-yellow-700">
                      <Clock className="w-4 h-4" /> Pending
                    </span>
                  )}
                </td>
                <td className="px-4 py-3">
                  <div className="flex gap-2">
                    {!report.verified && (
                      <button
                        className="p-1.5 rounded hover:bg-green-100 text-green-600 transition-colors"
                        title="Verify"
                      >
                        <CheckCircle2 className="w-4 h-4" />
                      </button>
                    )}
                    <button
                      className="p-1.5 rounded hover:bg-blue-100 text-blue-600 transition-colors"
                      title="Forward to Authority"
                    >
                      <Send className="w-4 h-4" />
                    </button>
                    <button
                      className="p-1.5 rounded hover:bg-red-100 text-red-600 transition-colors"
                      title="Delete"
                    >
                      <Trash2 className="w-4 h-4" />
                    </button>
                  </div>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
}
